module.exports = {
  path: '/test',
  component: (resolve) => import('components/EmptyTmpl').then(module => resolve(module)),
  children: [
    {
      path: 'SkeletonFinanceSubMenuMore',
      name: 'TestSkeletonFinanceSubMenuMore',
      component: (resolve) => import('components/skeleton/SkeletonFinanceSubMenuMore').then(module => resolve(module)),
      meta: {
        title: 'SkeletonFinanceSubMenuMore'
      }
    },
    {
      path: 'SkeletonInvestmentFinanceHome',
      name: 'TestSkeletonInvestmentFinanceHome',
      component: (resolve) => import('components/skeleton/SkeletonInvestmentFinanceHome').then(module => resolve(module)),
      meta: {
        title: 'SkeletonInvestmentFinanceHome'
      }
    },
    {
      path: 'SkeletonWalletHome',
      name: 'TestSkeletonWalletHome',
      component: (resolve) => import('components/skeleton/SkeletonWalletHome').then(module => resolve(module)),
      meta: {
        title: 'SkeletonWalletHome'
      }
    }
  ]
}



