<template>
  <transition name="list-fade">
    <div class="playlist" v-show="showFlag" @click="hide">
      <div class="list-wrapper" @click.stop>
        <div class="list-header">
          <h1 class="title">
            <i class="icon" :class="stateIcon" @click="changeMode"></i>
            <span class="text">播放模式</span>
            <span class="clear" @click="showConfirm"><i class="icon-clear"></i></span>
          </h1>
        </div>
        <scroll ref="listContent" :data="sequenceList" class="list-content" :refreshDelay="100">
          <transition-group ref="list" name="list" tag="ul">
            <li :key="item.id" class="item" v-for="(item,index) in sequenceList" @click="selectItem(item,index)" ref="oli">
              <i class="current" :class="getCurrentIcon(item)"></i>
              <span class="text">{{item.name}}</span>
              <span class="like" @click.stop="toggleFavorite(item)">
                <i :class="getFavoriteIcon(item)"></i>
              </span>
              <span class="delete" @click.stop="deleteOne(item)">
                <i class="icon-delete"></i>
              </span>
            </li>
          </transition-group>
        </scroll>
        <div class="list-operate">
          <div class="add" @click="showAdd">
            <i class="icon-add"></i>
            <span class="text">添加歌曲到队列</span>
          </div>
        </div>
        <div class="list-close" @click="hide">
          <span>关闭</span>
        </div>
      </div>
      <confirm ref="confirm" text="您确定要清空所有歌曲列表吗？" confirmBtnText="清空" @confirm="clearAllSongs"></confirm>
      <add-song ref="addSong"></add-song>
    </div>
  </transition>
</template>

<script type="text/ecmascript-6">
  import {mapActions} from 'vuex'
  import {playMode} from 'common/js/config'
  import scroll from 'base/scroll/scroll'
  import confirm from 'base/confirm/confirm'
  import addSong from 'components/add-song/add-song'
  import {playerMixin} from 'common/js/mixin'
  export default {
    mixins:[playerMixin],
    data() {
      return{
        showFlag:false
      }
    },
    computed: {

    },
    methods: {
      showConfirm(){
        this.$refs.confirm.show()
      },
      show(){
        this.showFlag = true
        setTimeout(() => {
          this.$refs.listContent.refresh()
          this._scrollToTop(this.currentSong)
        },20)
      },
      hide(){
        this.showFlag = false
      },
      getCurrentIcon(item){
        return this.currentSong.id === item.id ? 'icon-play' : ''
      },
      selectItem(item,index){
        if(this.mode === playMode.random){
          let index = this.playlist.findIndex((song)=>{
            return song.id === item.id
          })
        }
        this.setCurrentIndex(index)
        this.setPlayState(true)
      },
      deleteOne(item){
        this.deleteSong(item)
        if(!this.playlist.length){
          this.hide()
        }
      },
      clearAllSongs(){
        this.hide();
        this.clearAllSong()
      },
      showAdd(){
        this.$refs.addSong.show();
      },
      _scrollToTop(current){
        let index = this.sequenceList.findIndex((item) => {
          return current.id === item.id
        })
        this.$refs.listContent.scrollToElement(this.$refs.oli[index],0)
      },
      ...mapActions([
        'deleteSong',
        'clearAllSong'
      ])
    },
    watch: {
      currentSong(newSong,oldSong){
        if(!this.showFlag || newSong.id === oldSong.id){
          return
        }
        this._scrollToTop(newSong)
      }
    },
    components: {
      scroll,
      confirm,
      addSong
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"

  .playlist
    position: fixed
    left: 0
    right: 0
    top: 0
    bottom: 0
    z-index: 200
    background-color: $color-highlight-background
    &.list-fade-enter-active, &.list-fade-leave-active
      transition: opacity 0.3s
      .list-wrapper
        transition: all 0.3s
    &.list-fade-enter, &.list-fade-leave-to
      opacity: 0
      .list-wrapper
        transform: translate3d(0, 100%, 0)
    &.list-fade-enter
    .list-wrapper
      position: absolute
      left: 0
      bottom: 0
      width: 100%
      background: $color-background
      .list-header
        position: relative
        padding: 20px 30px 10px 20px
        .title
          display: flex
          align-items: center
          .icon
            margin-right: 10px
            font-size: 30px
            color: $color-theme-d
          .text
            flex: 1
            font-size: $font-size-medium
            color: $color-text
          .clear
            extend-click()
            .icon-clear
              font-size: $font-size-medium
              color: $color-text
      .list-content
        max-height: 240px
        overflow: hidden
        .item
          display: flex
          align-items: center
          height: 40px
          padding: 0 30px 0 20px
          overflow: hidden
          &.list-enter-active, &.list-leave-active
            transition: all 0.1s
          &.list-enter, &.list-leave-to
            height: 0
          .current
            flex: 0 0 20px
            width: 20px
            font-size: $font-size-small
            color: $color-theme-d
          .text
            flex: 1
            no-wrap()
            font-size: $font-size-medium
            color: $color-text-d
          .like
            extend-click()
            margin-right: 15px
            font-size: $font-size-small
            color: $color-theme
            .icon-favorite
              color: $color-sub-theme
          .delete
            extend-click()
            font-size: $font-size-small
            color: $color-theme
      .list-operate
        width: 140px
        margin: 20px auto 30px auto
        .add
          display: flex
          align-items: center
          padding: 8px 16px
          border: 1px solid $color-text-l
          border-radius: 100px
          color: $color-text-l
          .icon-add
            margin-right: 5px
            font-size: $font-size-small-s
          .text
            font-size: $font-size-small
      .list-close
        text-align: center
        line-height: 50px
        background: $color-background
        font-size: $font-size-medium-x
        color: $color-text-l
</style>



