<template>
  <transition name="slide">
    <div class="add-song" v-show="showFlag" @click.stop>
      <div class="header">
        <h1 class="title">添加歌曲到列表</h1>
        <div class="close" @click="hide">
          <i class="icon-close"></i>
        </div>
      </div>
      <div class="search-box-wrapper">
        <search-box ref="searchBox" placeholder="请搜素歌曲" @query="onQueryChange"></search-box>
      </div>
      <div class="shortcut" v-show="!query">
        <switches :currentIndex="currentIndex" :switches="switches" @switch="selectItem"></switches>
        <div class="list-wrapper">
          <scroll ref="playScroll" class="list-scroll" :data="playHistory" v-if="currentIndex === 0">
            <div class="list-inner">
              <song-list :songs="playHistory" @select="selectSong"></song-list>
            </div>
          </scroll>
          <scroll ref="historyScroll" class="list-scroll" :data="searchHistory" v-if="currentIndex === 1" :refreshDelay="100">
            <div class="list-inner">
              <search-list :searches="searchHistory" @select="addQuery" @delete="deleteHistory"></search-list>
            </div>
          </scroll>
        </div>
      </div>
      <div class="search-result" v-show="query">
        <suggest ref="suggest" :query="query" :showSinger="showSinger" @selectSearch="selectSuggest" @onScrollStart="hideKeyboard"></suggest>
      </div>
      <top-tip ref="topTip">
        <div class="tip-title">
          <i class="icon-ok"></i>
          <span class="text">1首歌已经添加到播放队列</span>
        </div>
      </top-tip>
    </div>
  </transition>
</template>

<script type="text/ecmascript-6">
  import searchBox from 'base/search-box/search-box'
  import suggest from 'components/suggest/suggest'
  import {searchMixin} from 'common/js/mixin'
  import switches from 'base/switches/switches'
  import scroll from 'base/scroll/scroll'
  import songList from 'base/song-list/song-list'
  import searchList from 'base/search-list/search-list'
  import {mapGetters,mapActions} from 'vuex'
  import Song from 'common/js/song'
  import topTip from 'base/top-tip/top-tip'
  export default{
    mixins:[searchMixin],
    components:{
      searchBox,
      suggest,
      switches,
      scroll,
      songList,
      searchList,
      topTip
    },
    computed:{
      ...mapGetters([
        'playHistory'
      ])
    },
    data(){
      return{
        showFlag:false,
        query:'',
        showSinger:false,
        currentIndex:0,
        switches:[
          {name:'最近播放'},
          {name:'搜索历史'}
        ]
      }
    },
    methods:{
      show(){
        this.showFlag = true
        setTimeout(() => {
          if(this.currentIndex === 0){
            this.$refs.playScroll.refresh()
          }else {
            this.$refs.historyScroll.refresh()
          }
        },20)
      },
      hide(){
        this.showFlag = false
      },
      selectItem(index){
        this.currentIndex = index
      },
      selectSong(song){
        this.insertSong(new Song(song))
        this.$refs.topTip.show()
      },
      selectSuggest(){
        this.saveHistory();
        this.$refs.topTip.show()
      },
      ...mapActions([
        'insertSong'
      ])
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"

  .add-song
    position: fixed
    top: 0
    bottom: 0
    width: 100%
    z-index: 200
    background: $color-background
    &.slide-enter-active, &.slide-leave-active
      transition: all 0.3s
    &.slide-enter, &.slide-leave-to
      transform: translate3d(100%, 0, 0)
    .header
      position: relative
      height: 44px
      text-align: center
      .title
        line-height: 44px
        font-size: $font-size-large
        color: $color-text
      .close
        position: absolute
        top: 0
        right: 8px
        .icon-close
          display: block
          padding: 12px
          font-size: 20px
          color: $color-theme

    .search-box-wrapper
      margin: 20px
    .shortcut
      .list-wrapper
        position: absolute
        top: 165px
        bottom: 0
        width: 100%
        .list-scroll
          height: 100%
          overflow: hidden
          .list-inner
            padding: 20px 30px
    .search-result
      position: fixed
      top: 124px
      bottom: 0
      width: 100%
    .tip-title
      text-align: center
      padding: 18px 0
      font-size: 0
      .icon-ok
        font-size: $font-size-medium
        color: $color-theme
        margin-right: 4px
      .text
        font-size: $font-size-medium
        color: $color-text
</style>



